import React, { Component } from 'react';
import { Router, Route, Link, browserHistory } from 'react-router';

import logo from './logo.svg';
import './App.css';
const Page = ({title})=> (
  <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>{title}</h2>
        </div>
        <div>
          <Link to="/" style={{marginRight: 25+'px'}}> Home </Link>    
          <Link to="/about" style={{marginRight: 25+'px'}}> News </Link>
          <Link to="/settings" style={{marginRight: 25+'px'}}> Sports </Link>
        </div>

        <p className="App-intro">
          I am {title} page
        </p>
        
      </div>
)

const Home = (props)=> (
  <div>
  <Page title="Home" />
  <p>Giganotosaurus ("giant southern lizard") is a genus of theropod dinosaur that lived in what is now Argentina, around 99.6 to 97 million years ago. It was one of the largest known terrestrial carnivores, but the exact size has been hard to determine from the incomplete remains found so far. The holotype specimen, discovered in Patagonia in 1993, is almost 70% complete, and indicates a length of 12 to 13 m (39 to 43 ft), a skull 1.53 to 1.80 m (5.0 to 5.9 ft) in length, and a weight of 4.2 to 13.8 t (4.6 to 15.2 short tons). A length of 13.2 m (43 ft) has been extrapolated from another individual's dentary bone. Some researchers believe the animal to be larger than Tyrannosaurus, generally considered the largest theropod. The skull was low, with a ridge-like crest in front of the eye. The teeth were serrated, and the front of the lower jaw was flattened. Giganotosaurus is thought to have had a homeothermic metabolism, between that of a mammal and a reptile, which would have enabled rapid growth but not fast movement. It was probably the apex predator of its ecosystem, feeding on juvenile sauropod dinosaurs. (Full article...)</p>
  </div>
)
const About = (props)=> (
  <div>
  <Page title="News" />
  <div>
    <ol>
   <li>The Supreme Court of Pakistan disqualifies incumbent Prime Minister Nawaz Sharif (pictured) from holding public office following a corruption investigation.</li>
<li>In golf, Jordan Spieth wins the Open Championship.</li>
<li>The Women's Cricket World Cup concludes with England defeating India in the final.</li>
<li>In cycling, Chris Froome wins the Tour de France.</li>
<li>Ram Nath Kovind is elected President of India.</li>
<li>Iranian mathematician Maryam Mirzakhani, the first woman to receive the Fields Medal, dies at the age of 40.</li>
</ol>
    </div>
  </div>
)
const Settings = (props)=> (
  <div>
  <Page title="Sports" />
  <div>
    <strong>2017 AFL season</strong>
    <ol>
<li>2017 MLB season</li>
<li>2017 MLS season</li>
<li>2017 NWSL season</li>
<li>2017 PGA Tour</li>
<li>2017 Super Rugby season</li>
<li>2017 WNBA season</li>
<li>2017–18 Snooker season</li>
</ol>
    </div>
  </div>
)

class App extends Component {
  render() {
    return (
      <Router history={browserHistory}>
        <Route path="/" component={Home} />
        <Route path="/about" component={About} />
        <Route path="/settings" component={Settings} />

        </Router>
    );
  }
}

export default App;



